import { Component, OnInit, ViewChild } from '@angular/core';
import { asBlob } from 'html-docx-js-typescript';
import { FileSaverService } from 'ngx-filesaver';
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
} from '@angular/forms';
import { MatDatepickerInputEvent } from '@angular/material/datepicker';
import { AlertDialogComponent } from 'src/app/shared/pages/alert-dialog/alert-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DatePipe } from '@angular/common';

import { YeuCauSuDungPTNModel } from 'src/app/shared/models/yeu-cau-su-dung-ptn.model';
import { LoaiHocVienModel } from 'src/app/shared/models/loai-hoc-vien.model';
import { YeuCauSuDungPTNService } from './yeu-cau-su-dung-ptn.service';
import { MatStepper } from '@angular/material/stepper';
import { NoiQuanLyModel } from 'src/app/shared/models/noi-quan-ly.model';
import { GiaoVienModel } from 'src/app/shared/models/giao-vien.model';
import { environment } from 'src/environments/environment';
import {YeuCauSuDungPTNHTML} from 'src/assets/form_n_rule/yeu-cau-su-dung-ptn'
const htmlString = `<!DOCTYPE html>
<html lang="en">
<head>
  <meta charset="UTF-8">
  <title>Document</title>
</head>
<body>
  <h1>Welcome</h1>
</body>
</html>`;
const opt = {
  margin: {
    top: 100,
  },
  orientation: 'landscape', // type error: because typescript automatically widen this type to 'string' but not 'Orient' - 'string literal type'
};

@Component({
  selector: 'app-yeu-cau-su-dung-ptn',
  templateUrl: './yeu-cau-su-dung-ptn.component.html',
  styleUrls: ['./yeu-cau-su-dung-ptn.component.scss'],
})
export class YeuCauSuDungPTNComponent implements OnInit {
  _loaiHocViens: LoaiHocVienModel[];
  _noiQuanLys: NoiQuanLyModel[];
  _giaoViens: GiaoVienModel[];
  _yeuCau = new YeuCauSuDungPTNModel();
  showProgressBar = false;
  _hasValueStartDay = false;
  _hasValueEndDay = false;

  _minDate = new Date();
  _maxDate = new Date();
  campaignTwo: FormGroup;

  thongTinCoBanGroup: FormGroup;
  deTaiGroup: FormGroup;
  HVCH_NCSGroup: FormGroup;
  camKetGroup: FormGroup;
  isEditable = true;

  myFilter = (d: Date | null): boolean => {
    const day = (d || new Date()).getDay();
    // Prevent Saturday and Sunday from being selected.
    return day !== 0 && day !== 6;
  };
  @ViewChild(MatStepper) stepper: MatStepper;
  constructor(
    private _formBuilder: FormBuilder,
    public datepipe: DatePipe,
    private __service: YeuCauSuDungPTNService,

    private _alertDialog: MatDialog,
    private __snackbar: MatSnackBar,
    private _FileSaverService: FileSaverService
  ) {}
    html = YeuCauSuDungPTNHTML
  ngOnInit() {
    this.thongTinCoBanGroup = this._formBuilder.group({
      hoTen: ['', Validators.required],
      ngaySinh: ['', Validators.required],
      MSSV: ['', Validators.required],
      email: ['', Validators.email],
      loaiHocVien: ['', Validators.required],
      SDT: ['', Validators.required],
    });
    this.deTaiGroup = this._formBuilder.group({
      tenDeTai: ['', Validators.required],
      maGVHD: ['', Validators.required],
      thoiGianBatDau: ['', Validators.required],
      thoiGianKetThuc: ['', Validators.required],
      noiQuanLy: ['', Validators.required],
    });
    this.HVCH_NCSGroup = this._formBuilder.group({
      tenHVCH_NCS: ['', Validators.nullValidator],
      maHVCH_NCS: ['', Validators.nullValidator],
    });
    this.camKetGroup = this._formBuilder.group({
      camKet: ['', Validators.required],
    });
    this._maxDate.setDate(this._minDate.getDate() + 30);
    this.campaignTwo = new FormGroup({
      start: new FormControl(new Date()),
      end: new FormControl(new Date()),
    });

    this.getDataLoaiHocViens();
    this.getDataNoiQuanLys();
    this.getDataConsts();
    this.getDataGiaoVien();
  }
  save() {
    asBlob(YeuCauSuDungPTNHTML).then((data) => {
      this.saveFile(data as Blob);
    });
  }
  private saveFile(data: Blob) {
    const fileName = `save.docx`;
    const fileType = this._FileSaverService.genType(fileName);
    // const txtBlob = new Blob([this.text], { type: fileType });
    this._FileSaverService.save(data, fileName);
  }

  private getDataConsts() {
    this.showProgressBar = true;
    this.__service.getAllConst().subscribe(
      (data) => {
        data.forEach((element) => {
          if (element.Name == environment.constant.MaxDateUseLab) {
            this._maxDate.setDate(new Date(element.Value).getDate());
          }
        });

        this.showProgressBar = false;
      },
      (error) => {
        debugger;
        this.showProgressBar = false;
      }
    );
  }
  private getDataLoaiHocViens() {
    this.showProgressBar = true;
    this.__service.getAllLoaiHocVien().subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this._loaiHocViens = data;
        this._loaiHocViens.sort((a, b) => {
          if (a.Ten >= b.Ten) return 1;
          else return -1;
        });
      },
      (error) => {
        debugger;
        this.showProgressBar = false;
      }
    );
  }
  private getDataNoiQuanLys() {
    this.showProgressBar = true;
    this.__service.getAllNoiQuanLy().subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this._noiQuanLys = data;
        this._noiQuanLys = this._noiQuanLys.filter((f) => f.HienThi);
        this._noiQuanLys.sort((a, b) => {
          if (a.TenNoiQuanLy >= b.TenNoiQuanLy) return 1;
          else return -1;
        });
      },
      (error) => {
        debugger;
        this.showProgressBar = false;
      }
    );
  }
  private getDataGiaoVien() {
    this.showProgressBar = true;
    this.__service.getAllGiaoVien().subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this._giaoViens = data;
        this._giaoViens.sort((a, b) => {
          if (a.TenGiaoVien >= b.TenGiaoVien) return 1;
          else return -1;
        });
      },
      (error) => {
        this.showProgressBar = false;
      }
    );
  }
  print() {}
  confirm() {
    if (
      this.camKetGroup.value.camKet.toLocaleLowerCase() !=
      'trong quá trình thực hiện đề tài tại phòng thí nghiệm, Tôi xin cam kết thực hiện đúng nội qui và quy định của PTN'.toLocaleLowerCase()
    ) {
      this.__snackbar.open('Vui lòng nhập đúng cam kết', null, {
        duration: 2000,
      });
    } else {
      this.showProgressBar = true;
      this._yeuCau.HoTen = this.thongTinCoBanGroup.value.hoTen;
      this._yeuCau.MaSo = this.thongTinCoBanGroup.value.MSSV;
      this._yeuCau.Email = this.thongTinCoBanGroup.value.email;
      this._yeuCau.SDT = this.thongTinCoBanGroup.value.SDT;
      this._yeuCau.TenDeTai = this.deTaiGroup.value.tenDeTai;
      this._yeuCau.MaGVHD = this.deTaiGroup.value.maGVHD;
      this._yeuCau.ThoiGianBatDau = this.deTaiGroup.value.thoiGianBatDau;
      this._yeuCau.TenHVCH_NCS = this.HVCH_NCSGroup.value.tenHVCH_NCS;
      this._yeuCau.MaHVCH_NCS = this.HVCH_NCSGroup.value.maHVCH_NCS;

      this.__service.add(this._yeuCau).subscribe(
        (data) => {
          this.showProgressBar = false;
          let res = this._yeuCau;
          this.stepper.reset();
          if (data === 'true') {
            this.__snackbar.open('Đăng ký thành công', null, {
              duration: 5000,
            });
          } else {
            this.__snackbar.open(data, null, {
              duration: 2000,
            });
          }
          // this.dialogMainRef.close(
          //   {
          //     data: res,
          //     result: true
          //   }
          // )
        },
        (error) => {
          //Khả năng là do trùng mã (không hiển thị)
          this.showProgressBar = false;
          this._alertDialog.open(AlertDialogComponent, {
            disableClose: true,
            width: '17rem',
            data: {
              dataKey: 'Có lỗi xảy ra',
            },
          });
        }
      );
    }
  }

  addEventNgayBatDau(type: string, event: MatDatepickerInputEvent<Date>) {
    this._yeuCau.ThoiGianBatDau = this.datepipe.transform(event.value, 'short');
    this._hasValueStartDay = true;
  }
  addEventNgayKetThuc(type: string, event: MatDatepickerInputEvent<Date>) {
    this._yeuCau.ThoiGianKetThuc = this.datepipe.transform(
      event.value,
      'short'
    );
    this._hasValueEndDay = true;
    // const datChoNgayBatDau = new Date(this._yeuCau.ThoiGianBatDau).getDate()
    // if (this._selectedThietBiChiTiet.NgayBatDau && this._selectedThietBiChiTiet.NgayKetThuc) {
    //   const ngayBatDau = new Date(this._selectedThietBiChiTiet.NgayBatDau).getDate()
    //   if (datChoNgayBatDau < ngayBatDau) {
    //     const ngayKetThuc = new Date(this._selectedThietBiChiTiet.NgayKetThuc).getDate()
    //     const datChoNgayKetThuc = new Date(this._datCho.NgayKetThuc).getDate()
    //     if (datChoNgayKetThuc > ngayKetThuc) {
    //       //không được phép đặt chỗ như vậy
    //       this.fromInput.value = ''
    //       this.toInput.value = ''
    //       this.__snackbar.open('Vui lòng chọn ngày hợp lệ', null, {
    //         duration: 2000,
    //       });
    //     }
    //   }
    // }
  }
  addEventNgaySinh(type: string, event: MatDatepickerInputEvent<Date>) {
    this._yeuCau.NgaySinh = this.datepipe.transform(event.value, 'short');
  }
}
